.pip-drilldown-list {
  .pip-drilldown-list-item {
    height: 48px;
    width: 100%;
    margin: 0 !important;
    padding-left: 24px;
    padding-right: 16px;
    border-radius: 0 !important;

    md-icon {
      @include flex-fixed();
      min-width: 24px;
    }

    .pip-content {
      @include flex();
      @include flex-direction(row);
      @include flex-vertical-content(center);
      width: 100%;
    }

    &.pip-selected {
      padding-left: 19px;
      background-color: rgba(158, 158, 158, 0.2);
      border-left: 5px solid rgba(33, 150, 243, 0.5) !important;
    }
  
    @media (max-width: 768px) {
      padding-left: 16px;
      padding-right: 10px;
      
      &.pip-selected{
        padding-left: 11px;
      }
    }
  
    @include flex();
    @include flex-direction(row);
    @include flex-vertical-content(center);
    @include flex-justify-content(start, flex-start);
  
    text-transform: none !important;//capitalize;
    font: $font-subhead2;
  
    .pip-title {
      text-align: justify;
      text-align: start;
      @include flex();
      @include text-overflow();
      margin: 0;
      margin-right: 16px;

      flex: 1;
      -webkit-flex: 1;
      -ms-flex: 1;

      box-sizing: border-box;
    }

    .pip-subtitle {
      font: $font-body1;
      opacity: $opacity-secondary;
      text-align: justify;
      text-align: start;
      margin: 0 24px 0 0;
      overflow: hidden;
      text-overflow: ellipsis;        
    }

    .pip-icon {
      margin-right: 26px;
    }

    &.pip-drilldown-list-item-lg {
      height: 64px;
      .pip-title {
        margin-top: 1px !important;
        margin-bottom: 4px !important;
      }
      .pip-subcontent {
        @include text-overflow();
      }
    }
  }
}